<template>
  <div class="phoneContainer">
    <div class="modalBack" v-if="ModalWindow1 == 1">
      <div class="modalWindow">
        <h2 class="word2">무엇을 하시겠습니까?</h2>
        <div class="modalGroup">
          <router-link to="/cs">
            <button class="modalBtn">요약</button>
          </router-link>
          <button class="modalBtn" @click="ModalWindow1 = 0">읽어주기</button>
        </div>
      </div>
    </div>
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="goBack"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
      <h2 class="word1">캡처 화면</h2>
    </div>
    <div class="captureContainer">
      <img
        v-if="imageDataUrl"
        :src="imageDataUrl"
        alt="캡처된 이미지"
        class="captureDisplay"
      />
    </div>
    <div class="buttonGroup1">
      <router-link to="/cm">
        <button class="captureBtn2">재촬영</button>
      </router-link>
      <button @click="ModalWindow1 = 1" class="captureBtn2">완료</button>
    </div>
  </div>
</template>

<script>
  import { useImageStore } from "../stores/imageStore"

  export default {
    name: 'CameraImage',
    components: {
    },
    data() {
      return {
        currentTime: null,
        imageDataUrl: useImageStore().imageDataUrl,
        ModalWindow1: 0
      }
    },
    mounted() {
      this.updateTime();
      this.timer = setInterval(this.updateTime, 60000);
    },
    methods: {
    
    goBack() {
      this.$router.back();
    },
    updateTime() {
        const now = new Date();
        const hours = now.getHours().toString().padStart(2, '0');
        const minutes = now.getMinutes().toString().padStart(2, '0');
        this.currentTime = `${hours}:${minutes}`;
    }
   }
  }
</script>

<style>
@import "../css/index.css";
</style>
