<template>
  <div class="phoneContainer">
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="goBack"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
      <h2 class="word1">내용 추출 완료!</h2>
    </div>
    <div class="infDiv">
      <p class="pre-wrap">{{ detail1 }}</p>
      <p class="pre-wrap">{{ detail2 }}</p>
    </div>
    <div class="buttonGroup1">
      <button class="shortBtn1">+ 확대</button>
      <button class="shortBtn1">- 축소</button>
    </div>
    <button class="longBtn1">전체 내용 확인</button>
    <button class="longBtn1">일정 추가</button>
  </div>
</template>

<script>
  export default {
    name: 'CameraSummary',
    components: {
    },
    data() {
      return {
        currentTime: null,
        detail1: "두산 vs 삼성\n2024/05/02 (목) 오후 6시 30분\n1루 FILA zone",
        detail2: "206블럭 13열 143번\n일반\n18000원(무통장 입금)"
      }
    },
    mounted() {
      this.updateTime();
      this.timer = setInterval(this.updateTime, 60000);
    },
    methods: {
    
      goBack() {
        this.$router.back();
      },
      updateTime() {
        const now = new Date();
        const hours = now.getHours().toString().padStart(2, '0');
        const minutes = now.getMinutes().toString().padStart(2, '0');
        this.currentTime = `${hours}:${minutes}`;
      }
    }
  }
</script>

<style>
@import "../css/index.css";
</style>
