<template>
  <div class="phoneContainer">
    <div class=withBack>
      <img
          role="button"
          tabindex="0"
          alt=""
          @click="goBack"
          @keyup="goBack"
          class="backImg"
          src="../assets/뒤로가기.png"
        />
    </div>
      <div>
          <video ref="videoElement" class="cameraDisplay" autoplay playsinline></video>
          <br><br>
          <button class="captureBtn" @click="switchCamera">전환</button>
          <router-link to="/ci">
              <button class="captureBtn" @click="capture">촬영</button>
          </router-link>
          <canvas ref="canvas" style="display: none;"></canvas>
      </div>
  </div>
</template>

<script>
  import { useImageStore } from "../stores/imageStore"

  export default {
    name: 'CameraFirst',
    components: {
    },
    data() {
      return { 
          currentTime: null
      };
    },
    mounted() {
      this.updateTime();
      this.getDevices();
      this.timer = setInterval(this.updateTime, 60000);
    },
    methods: {
      async getDevices() {
        const devices = await navigator.mediaDevices.enumerateDevices();
        this.devices = devices.filter(device => device.kind === 'videoinput');
        if (this.devices.length > 0) {
          this.currentDeviceId = this.devices[0].deviceId;
          this.setCameraStream();
        }
      },
      async setCameraStream() {
        try {
          const stream = await navigator.mediaDevices.getUserMedia({
          video: { deviceId: this.currentDeviceId ? { exact: this.currentDeviceId } : undefined }
        });
        this.$refs.videoElement.srcObject = stream;
      } catch (error) {
        console.error('Error accessing the camera', error);
      }
    },
    switchCamera() {
      let currentIndex = this.devices.findIndex(device => device.deviceId === this.currentDeviceId);
      if (currentIndex !== -1) {
        let nextIndex = (currentIndex + 1) % this.devices.length;
        this.currentDeviceId = this.devices[nextIndex].deviceId;
        this.setCameraStream();
      }
    },
    goBack() {
      this.$router.back();
    },
    async startCamera() {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({ video: true });
        this.$refs.videoElement.srcObject = stream;
      } catch (err) {
        console.error("에러가 발생했습니다.", err);
      }
    },
    capture() {
      const video = this.$refs.videoElement;
      const canvas = this.$refs.canvas;
      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;
      canvas.getContext('2d').drawImage(video, 0, 0, canvas.width, canvas.height);
      // 이미지를 저장하거나 처리하기
      this.saveImage(canvas);
      const imageDataUrl = canvas.toDataURL('image/png');
      const imageStore = useImageStore();
      imageStore.setImageDataUrl(imageDataUrl);
    },
    saveImage(canvas) {      
      const imageDataUrl2 = canvas.toDataURL('image/png');
      // a 태그를 생성하여 다운로드 링크를 구성
      const link = document.createElement('a');
      link.href = imageDataUrl2;
      link.download = 'capture.png'; // 다운로드될 파일의 이름 지정
      document.body.appendChild(link); // DOM에 링크 추가
      link.click(); // 클릭 이벤트를 발생시켜 다운로드
      document.body.removeChild(link); // 사용 후 링크 삭제
    },
    updateTime() {
        const now = new Date();
        const hours = now.getHours().toString().padStart(2, '0');
        const minutes = now.getMinutes().toString().padStart(2, '0');
        this.currentTime = `${hours}:${minutes}`;
    }
   }
  }
</script>
  
<style>
@import '../css/index.css';
</style>