<template>
  <div class="phoneContainer">
    <!-- 수정된 부분: 상단바 -->
    <div class=withBack>
      <p class="pageTitle">안양시 안내</p>
      <img role="button" tabindex="0" alt="" @click="goBack" @keyup="goBack" class="backImg" src="../assets/뒤로가기.png"/>
    </div> 
    <!-- 수정 종료 -->
    <!-- 검색 바 -->
    <div class="searchBarContainer">
      <input type="text" v-model="searchKeyword" placeholder="검색어를 입력해주세요" class="searchBar" />
      <button @click="search" class="searchButton">검색</button>
    </div>
    <!-- 공지사항 게시판 -->
    <div class="noticeBoard">
      <div class="noticeItem" v-for="(notice, index) in visibleNotices" :key="index">
        <!-- An1.vue로 넘어가는 기능 추가 -->
        <p class="noticeTitle" @click="goToAn1(notice.title)">{{ notice.title }}</p>
      </div>
    </div>
    <!-- 게시판 네비게이션 -->
    <div class="boardNavigation">
      <!-- 이전 페이지로 이동 버튼 -->
      <button @click="prevPage">&lt;</button>
      <!-- 페이지 번호 버튼 -->
      <button v-for="pageNumber in visiblePageNumbers" :key="pageNumber" @click="goToPage(pageNumber)">
      {{ pageNumber }}
      </button>
      <!-- 다음 페이지로 이동 버튼 -->
      <button @click="nextPage">&gt;</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AnnounceList',
  components: {
  },
  data() {
    return { 
      currentTime: null, 
      currentPage: 1,
      itemsPerPage: 5,
      totalPages: 0,
      searchKeyword: '',
      notices: [
        {
          title: "[건강정보] 만안구 보건소 독감 접종 안내"
        },
        {
          title: "[공지사항] 만안구 안양 2동 공지사항"
        },
        {
          title: "[공사안내] 만안구 배수로 공사 일정"
        },
        {
          title: "[채용공고] 노인 일자리 안내"
        },
        {
          title: "[행사] 전국 노래자랑 안양편 일정"
        }
      ]

    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  computed: {
    visibleNotices() {
      const startIndex = (this.currentPage - 1) * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      return this.notices.slice(startIndex, endIndex);
    },
    visiblePageNumbers() {
      const pageRange = 3;
      let startPage = Math.max(1, this.currentPage - pageRange);
      let endPage = Math.min(this.totalPages, this.currentPage + pageRange);
      if (endPage - startPage < pageRange * 2) {
        startPage = Math.max(1, endPage - pageRange * 2);
      }
      return Array.from({ length: endPage - startPage + 1 }, (_, index) => startPage + index);
    }
  },
  methods: {
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, '0');
      const minutes = now.getMinutes().toString().padStart(2, '0');
      this.currentTime = `${hours}:${minutes}`;
    },
    goBack() {
      this.$router.back();
    },
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
    goToPage(pageNumber) {
      this.currentPage = pageNumber;
    },
    search() {
      console.log("검색어:", this.searchKeyword);
      // 추가적인 검색 로직을 구현할 수 있습니다.
    },
    goToAn1(title) {
      if (title === "[건강정보] 만안구 보건소 독감 접종 안내") {
        this.$router.push({ name: 'An1' });
      }
    }
  },
  watch: {
    notices() {
      this.totalPages = Math.ceil(this.notices.length / this.itemsPerPage);
    }
  }
}
</script>
  
<style>
  /* index.css 파일 import */
  @import "../css/index.css"; /* 추가됨 */
</style>